import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { DashboardService } from "../services/dashboard.service";
import { ToastrService } from "ngx-toastr";
@Component({
  selector: "app-dashboard",
  templateUrl: "./dashboard.component.html",
  styleUrls: ["./dashboard.component.css"]
})
export class DashboardComponent implements OnInit {
  showList = true;
  type = "slow";
  // dataSubscription: Subscription;
  data = [];
  public mfRowsOnPage: number = 20;
  public mfActivePage: number = 1;
  public mfSortBy: any = "";
  public mfSortOrder: string = "asc";

  constructor(
    public _router: Router,
    public dashboardService: DashboardService,
    public toastr: ToastrService
  ) {}

  ngOnInit() {
    this.dashboardService.pageType = '';
    // this.toastr.success('Hello world!', 'Toastr fun!');
    this.dashboardService.getDashData().subscribe(response => {
      console.log(response, "response");
      if (response["success"]) {
        this.data = response["data"];
      } else {
        this.toastr.error(response["message"], "Warning");
      }
    });
  }

  selectedValues(index) {
    this.showList = false;
    this.dashboardService.selectedRecord = index;
    this._router.navigate(["/feeding"]);
  }

  selectedType(t) {
    this.type = t;
  }

  save() {
    this._router.navigate(["/feeding"]);
  }
  goToFeed(type,item){
    console.log(type,item);
    this.dashboardService.selectedRecord = type;
    this.dashboardService.data = item;
    this.dashboardService.pageType = 'dashboard';
    this._router.navigate(["/feeding"]);
  }

  ngOnDestroy() {
    // this.dataSubscription.unsubscribe()
  }
}
