import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DashboardService } from '../services/dashboard.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-feeding',
  templateUrl: './feeding.component.html',
  styleUrls: ['./feeding.component.css']
})
export class FeedingComponent implements OnInit {
  type = 1;
  actionType = 1;
  record = [];
  // dataSubscription: Subscription;
  data = {};
  remarks: any = '';

  constructor(
    public dashboardService: DashboardService,
    public router: Router,
    public toastr: ToastrService
  ) {}

  ngOnInit() {
    // redirect user to homepage if no selected record exists
    if (
      this.dashboardService.selectedRecord == null &&
      this.dashboardService.pageType == ''
    ) {
      this.router.navigate(['/dashboard']);
    } else if (this.dashboardService.pageType == 'dashboard') {
      // get data from service
      console.log(this.dashboardService.selectedRecord);
      console.log(this.dashboardService.data);
      const predData = [];
      this.dashboardService.columnNames.map(a => {
        predData.push(this.dashboardService.data[a.column_name]);
      });
      const body = {
        type: this.dashboardService.selectedRecord,
        id: this.dashboardService.data['id'],
        predData: predData
      };
      this.dashboardService.predictAutoManual(body).subscribe(response => {
        console.log(response, 'response');
        if (response['success']) {
          this.type = Number(response['predction']);
        } else {
          this.toastr.error(response['message'], 'Warning');
        }
      });
    } else {
      // pending, history
      this.type = this.dashboardService.data.predict;
      this.actionType = this.dashboardService.data.actual;
      this.remarks = this.dashboardService.data.remarks;
    }
  }

  goBack() {
    if (this.dashboardService.pageType === 'dashboard') {
      this.router.navigate(['/dashboard']);
    } else {
      this.router.navigate(['/feedback']);
    }
  }

  selectedType(t) {
    if (this.dashboardService.pageType === 'history') {
      return;
    }
    this.actionType = t;
  }

  Submit() {
    const body = {
      sh_id: this.dashboardService.data['sh_id'] || 0,
      id: this.dashboardService.data['id'],
      predict: this.type,
      actual: this.actionType,
      remarks: this.remarks,
      status: 1
    };

    this.dashboardService.submitAutoManual(body).subscribe(response => {
      console.log(response, 'submit response');
      if (response['success']) {
        this.toastr.success(response['message'], 'Success');
        if (this.dashboardService.pageType == 'dashboard') {
          this.router.navigate(['/dashboard']);
        } else {
          this.router.navigate(['/feedback']);
        }
      } else {
        this.toastr.error(response['message'], 'Warning');
      }
    });
  }

  ngOnDestroy() {
    // if (this.dataSubscription) {
    //   this.dataSubscription.unsubscribe();
    // }
  }
}
