import { Injectable } from "@angular/core";
import { DashboardService } from './dashboard.service';
import { ApiService } from './api.service';
import { Appsettings } from '../app.settings';

@Injectable({
  providedIn: "root"
})
export class KModelService {

  constructor(public dashboardService: DashboardService, public apiService: ApiService) {}

  // build model
  buildModel() {
    console.log("in kmodel buildmodel");
    this.apiService.callApi(Appsettings.API.BUILD_MODEL, 'GET').subscribe(data => {
      console.log(`data from build model`, data);
      
    })


  }
}
