import { Component, OnInit } from '@angular/core';
import { Chart } from 'angular-highcharts';
import { KModelService } from '../services/kmodel';


@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {

  constructor(public kmodel: KModelService) {

  }
  options: Object;
  tolerance: any = 10;
  graphData = [];
  chart: any;

  ngOnInit() {
    this.graphData.push(this.tolerance);
    this.graph();
  }


  graph() {
    this.options = {
      chart: {
        type: 'solidgauge'
      },
      title: 'test',
      pane: {
        center: ['50%', '85%'],
        size: '140%',
        startAngle: -90,
        endAngle: 90,
        background: {

          innerRadius: '60%',
          outerRadius: '100%',
          shape: 'arc'
        }
      },
      tooltip: {
        enabled: false
      },
      // the value axis
      yAxis: {
        stops: [
          [0.1, '#55BF3B'], // green
          [0.5, '#DDDF0D'], // yellow
          [0.9, '#DF5353'] // red
        ],
        lineWidth: 0,
        minorTickInterval: null,
        tickAmount: 2,
        title: {
          y: -70
        },
        labels: {
          y: 16
        },
        min: 0,
        max: 200,
        titles: {
          text: 'Speed'
        }
      },
      plotOptions: {
        solidgauge: {
          dataLabels: {
            y: 5,
            borderWidth: 0,
            useHTML: true
          }
        }
      },
      credits: {
        enabled: false
      },

      series: [{
        name: 'Speed',
        data: this.graphData,
        tooltip: {
          valueSuffix: ' km/h'
        }
      }]
    };

  }

  buildModel() {
    this.kmodel.buildModel();
  }

}
