import { Component, OnInit } from "@angular/core";
import { DashboardService } from "../services/dashboard.service";
import { ToastrService } from "ngx-toastr";
import * as moment from "moment";
import { Router } from "@angular/router";

@Component({
  selector: "app-feedback",
  templateUrl: "./feedback.component.html",
  styleUrls: ["./feedback.component.css"]
})
export class FeedbackComponent implements OnInit {
  // tabType: any = this.dashboardService.pageType || 'pending';
  tabType: any = 'pending';
  pendingData: any = [];
  historyData: any = [];

  public mfRowsOnPage: number = 20;
  public mfActivePage: number = 1;
  public mfSortBy: any = "";
  public mfSortOrder: string = "asc";

  public mfRowsOnPage2: number = 20;
  public mfActivePage2: number = 1;
  public mfSortBy2: any = "";
  public mfSortOrder2: string = "asc";
  constructor(
    public dashboardService: DashboardService,
    public toastr: ToastrService,
    public _router: Router
  ) {}
  showList = false;
  predictedType: any = {
    1: "Slow",
    2: "Fast",
    3: "Stop"
  };
 ngOnInit() {
    this.dashboardService.pageType = "";
    this.getFeedbackData();
  }
  ShowData() {
    this.showList = true;
  }
  backList() {
    this.showList = false;
  }

  getFeedbackData() {
    const body = {
      type: this.tabType
    };
    
    if (this.tabType === "history") {
      body["start"] = moment().format("YYYY-MM-DD");
      body["end"] = moment().format("YYYY-MM-DD");
    }
    console.log(body, "bodyy");
    
    this.dashboardService.getFeedbackData(body).subscribe(response => {
      console.log(response, "response");
      if (response["success"]) {
        if (this.tabType === "pending") {
          this.pendingData = JSON.parse(JSON.stringify(response["data"]));
        } else if (this.tabType === "history") {
          this.historyData = JSON.parse(JSON.stringify(response["data"]));
        }
      } else {
        this.toastr.error(response["message"], "Warning");
      }
    });
  }

  goToFeed(type, item) {
    console.log(type, item);
    this.dashboardService.pageType = type;
    this.dashboardService.data = item;
    this._router.navigate(["/feeding"]);
  }
}
