import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpModule } from '@angular/http';
import { ChartModule } from 'angular2-highcharts';
import { CalendarModule } from 'primeng/calendar';
import { ToastrModule } from 'ngx-toastr';
import {DataTableModule} from "angular-6-datatable";


import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { SettingsComponent } from './settings/settings.component';
import { FeedbackComponent } from './feedback/feedback.component';
import { FeedingComponent } from './feeding/feeding.component';
import { HeaderComponent } from './common/header/header.component';
declare var require;

export function highchartsFactory() {
  const Highcharts = require('highcharts');
  const More = require('highcharts/highcharts-more');
  const threeD = require('highcharts/highcharts-3d');
  const Exporting = require('highcharts/modules/exporting');
  const Solid = require('highcharts/modules/solid-gauge');
  const Heatmap = require('highcharts/modules/heatmap');
  const Funnel = require('highcharts/modules/funnel');
  Funnel(Highcharts);
  More(Highcharts);
  threeD(Highcharts);
  Heatmap(Highcharts);
  Exporting(Highcharts);
  Solid(Highcharts);
  return Highcharts;
}


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    DashboardComponent,
    SettingsComponent,
    FeedbackComponent,
    FeedingComponent,
    HeaderComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    AppRoutingModule,
    BrowserAnimationsModule, // animations module
    ToastrModule.forRoot(), // ToastrModule added
    CalendarModule,
    FormsModule,
    ReactiveFormsModule,
    ChartModule.forRoot(
      require('highcharts'),
      require('highcharts/modules/exporting'),
      require('highcharts/highcharts-more'),
      require('highcharts/modules/solid-gauge')
    ),
    DataTableModule],
  providers: [
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
