import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { map, switchAll } from 'rxjs/operators';
import { Router } from '@angular/router';
import { Appsettings } from '../app.settings';
@Injectable({
  providedIn: 'root'
})
export class ApiService {

  page = 'dashboard';
  callRefresh = false;
  // headers = { headers: new Headers({ 'content-type': 'application/Json' }) };
  // options = new RequestOptions();

  constructor(private _http: Http, public router: Router) {}

  callApi(url, method, body = null): Observable<any> {
    if (sessionStorage.getItem('token')) {
      this.token();
    }

    switch (method.toUpperCase()) {
      case 'LOGIN':
        return this._http.post(url, body).pipe(map((response: Response) => response.json()));
      case 'POST':
        return this._http.post(url, body, this.token()).pipe(map((response: Response) => response.json()));

      case 'PATCH':
        return this._http.patch(url, body, this.token()).pipe(map((response: Response) => response.json()));

      case 'DELETE':
        return this._http.delete(url, this.token()).pipe(map((response: Response) => response.json()));

      case 'GET':
        return this._http.get(url, this.token()).pipe(map((response: Response) => response.json()));
    }
  }
  token() {
    return new RequestOptions({
      headers: new Headers({
        'Content-Type': 'application/json',
        // Authorization: 'BEARER ' + sessionStorage.getItem('token')
      })
    });
  }
}
