import { ApiService } from "./api.service";
import { Appsettings } from "../app.settings";
import { Injectable } from "@angular/core";
import { Observable, BehaviorSubject } from "rxjs";
import { ToastrService } from 'ngx-toastr';
@Injectable({
  providedIn: "root"
})
export class DashboardService {
  // data: BehaviorSubject<Array<number>> = new BehaviorSubject([]);
  data :any = {};
  columnNames:any = [];
  cols = ['id',
  'tMacAcc-mean()-X',
  'tMacAcc-mean()-Y',
  'tMacAcc-mean()-Z', 'subject', 'Activity']
  selectedRecord = null;
  pageType:any = '';

  constructor(private _apiService: ApiService,
    public toastr: ToastrService) {
    this.getColumnNames().subscribe(response2 => {
      console.log(response2, "response");
      if (response2["success"]) {
        this.columnNames = response2["data"];
      } else {
        this.toastr.error(response2["message"], "Warning");
      }
    });
  }

  fetchRecords() {
    this._apiService.callApi(Appsettings.API.FETCH_RECORDS, 'GET', null).subscribe(data => {
      this.data.next(data.data);
    });
  }

  // get dashboard data
  getDashData(){
    return this._apiService.callApi(Appsettings.API.GET_DASH_DATA, 'GET', null);
  }

  // get column names
  getColumnNames(){
    return this._apiService.callApi(Appsettings.API.GET_COLUMN_NAMES, 'GET', null);
  }

  // predict auto manual
  predictAutoManual(body){
    return this._apiService.callApi(Appsettings.API.PREDICT_AUTO_MANUAL, 'POST', body);
  }
  // submit auto manual
  submitAutoManual(body){
    return this._apiService.callApi(Appsettings.API.SUBMIT_AUTO_MANUAL, 'POST', body);
  }
  // get feed back data by type
  getFeedbackData(body){
    return this._apiService.callApi(Appsettings.API.GET_FEEDBACK_DATA, 'POST', body);
  }
}
